.game_header {
  position: fixed;
  width: 100%;
  z-index: 2;
  background-color: #f8f9fa;
  top: 0;
}


.rank-badge {
  position: relative;
  text-align: center;
}

.dial {
  position: relative;
 
}

.label {
  font-size: x-small;
  color: #555555; /* Dark gray color for the label */
}

.progress-hexagon {
  fill: transparent;
  stroke-width: 2;
  stroke-linecap: round;
  stroke-dasharray: 390; /* Dash array for a complete hexagon */
  stroke-dashoffset: 390; /* Offset to hide the hexagon initially */
  transition: stroke-dashoffset 0.5s ease-in-out;
}

.red {
  stroke: #ff6961; /* Red color */
}

.white {
  stroke: #ffffff; /* White color */
}

.green {
  stroke: #77dd77; /* Green color */
}

.creditScore {
  display: flex;
  justify-content: center;
}

.creditScoreValue {
  font-size: small;
  fill: white;
}
.creditScoreTitle {
  font-size: xx-small;
  fill: white;
}

#signupForm .selected {
  border: 3px solid #cf0c0e;
  box-shadow: 0 0 5px rgba(207, 12, 14);
  transition: all 0.3s;
  /* width: 52px !important; */
  height: 52px !important;
}

.selected {
  border: 2px solid #cf0c0e;
  box-shadow: 0 0 5px rgba(207, 12, 14, 0.7);
  transition: all 0.3s;
}
.main-game-page-tabs button span {
  display: inline-block !important;
}
.main-game-page-tabs button:hover,
.main-game-page-tabs button[aria-selected='true'] {
  color: #101010 !important;
}

