.lottie-container div {
  width: 30px;
  height: 30px;
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 0;
}
.avatar img {
  border-radius: 6px;
}


.avatar-square {
  background: #b9bbbe;
    border: 3px solid #dee2e6;
    color: #f8f9fa;
    width: 60px;
    margin-left: 44px;
    display: flex;
    position: absolute;
    justify-content: center;
    align-items: center;
    height: 60px;
    border-radius: 9px;
    z-index: 2;
}
.fade-animation {
  transition: opacity 0.3s ease-in-out;
}

.fade-animation.fade-in {
  opacity: 1;
}

.fade-animation.fade-out {
  opacity: 0.8;
}

.main-game-page-tabs button span svg {
  margin-right: 5px !important;
}

.input-wrapper {
  position: relative;
}
.input-suffix {
  position: absolute;
  top: 60%;
  right: 10px;
  transform: translateY(-30%);
}

.pre-summary-panel {
  display: flex;
  overflow: hidden;
}

.pre-summary-panel__inner {
  display: flex;

  flex-wrap: nowrap;
  /* animation: ticker 195s linear infinite; */
  pointer-events: none;
}

.data-item {
  pointer-events: auto;
}

.mystery-box .data-item {
  margin-right: 10px !important;
}

@keyframes ticker {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(calc(-100% - 10px));
  }
}
.countdown-timer {
  width: 100%;
  height: 10px;
  border-radius: 10px;
  background-color: #020202;
  overflow: hidden;
}

div[role='tablist'] {
  min-height: unset !important;
}

.countdown-bar {
  height: 100%;
  background: linear-gradient(to top, #ff409a, #c438ef);
  animation: countdown 1.5s linear;
}

@keyframes countdown {
  0% {
    width: 100%;
  }
  100% {
    width: 0%;
  }
}

.game_header .main_header {
  padding: 0 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
/* 
.game_header .main_header {
  box-shadow: 0px 1px 2px #f0f0f0;
} */
.chat-wrapper div[role='tablist'] {
  border-bottom: solid 1px #f0f0f0;
}
.game_header .main_header a.game_logo {
  width: 127px;
  height: 34px;
  background-image: url('../../public/img/game-logo.svg');
  background-repeat: no-repeat;
  background-position: center center;
}
.game-type-panel .btn-game-type img {
  width: 160px !important;
  border-radius: 7px;
}

.game-type-panel .btn-game-type span {
  padding-right: 5px;
}
.game-type-panel .btn-game-type div.icon {
  width: 100%;
  align-items: center;
  font-size: 14px;
  font-weight: 500 !important;
}
.game_header .main_header #btn_leaderboards,
.game_header .main_header #btn_marketplace,

.game_header .main_header #btn_notifications
{
  display: flex;
  max-height: 50px;
  overflow: hidden;
  align-items: center;
}
.notification-container a{
  width: 30%;
  display: inline-block !important;
  margin: auto;
}
.notification-container a div:first-child{
  width: 60% !important;
  height: 60% !important;
  margin-bottom: 15px !important;
}
.notification-container .notification{
  width: 70%;
  display: inline-block;
}

.notification-container {
  display: flex;
  justify-content: start;
  margin-bottom: 10px;
  border-bottom: 2px solid;
}

.notification {
  display: flex;
  align-content: normal;
  
}
.notification p{
  font-weight: 600;
}

.notification-container:last-child {
  border-bottom: unset;
}

.notification .fromNow {
  font-size: 0.8em;
}

.game_header .main_header #btn_notifications.desktop-only,
.game_header .main_header #btn_leaderboards.desktop-only, .game_header .main_header #btn_marketplace.desktop-only,
.game_header .main_header #btn_bank.desktop-only
{
  margin-right: 10px;
}
.game_header .main_header #btn_notifications.mobile-only
{
  margin-right: -10px;

  margin-left: 15px;
}

.game_header .main_header #btn_leaderboards svg,
.game_header .main_header #btn_marketplace svg,

.game_header .main_header #btn_notifications svg{
  width: 20pt !important;
}
.table.main-game-table .table-row:hover {
  transform: scale(0.99);
  filter: brightness(1.09);
  transition: 0.2s;
  z-index: 999;
  cursor: pointer;
}

#wallet-btn {
  margin-top: -1px;
  border-radius: 0 0 0.2em !important;
}
#wallet-btn:hover {
  transform: unset !important;
}

#wallet-address {
  text-overflow: ellipsis;
  width: calc(100% - 10px);
  display: inline-block;
  direction: rtl;
}

.game-logs-modal-container .summary {
    display: flex;
    align-items: center;
}

.summary-flex {
    width: 50%;
    display: inline-flex;
    justify-content: space-between;
    flex-direction: row;
}

.summary-flex div {
    text-align: center;
    display: flex;
    flex-direction: column;
    padding: 5px 10px;
}

.summary-flex div span{
white-space: nowrap;
}

#balance {
  height: 32px;
  padding: 0px 0px 32px 8px;
  color: #212529;
  line-height: 28px;
  cursor: pointer;
  background: #e9ecef;
  font-size: 1em;
  font-weight: 600;
  white-space: nowrap;
  border: 1px solid #d81719;
  border-radius: 0.25em;
}
#balance svg {
  color: #fff;
}

.game_header .main_header .header_action_panel {
  display: flex;
  align-items: center;
}

.game_header .main_header .header_action_panel button {
  color: #060607;
  margin-left: 10px;
}

.custom-tab {
  position: relative;
}

.custom-tab::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0;
  height: 2px; /* Adjust the border height as needed */
  /* background-color: #ff0003; */
  transition: width 0.3s ease-in-out, left 0.3s ease-in-out;
  transform: translateX(-50%);
}

.custom-tab:hover::before {
  width: 100%;
  left: 0;
  transform: translateX(0%);
}

.chat-content .avatar {
  margin-bottom: 5px;
  margin-right: 5px !important;
}

.game_header .main_header .header_action_panel img.avatar,
.chat-content .avatar {
  width: 30px;
  height: 30px;
  border-radius: 9px;
  margin-right: 6px;
}

.game-page .pre-summary-panel > div.host-display-name {
  background-size: 2em;
}
