}

.game_wrapper {
  z-index: 1;
  width: 100%;
}

.contents_wrapper {
  margin-top: 70px;
  overflow-y: auto;
  background-color: #f2f3f5;
}

.hidden {
  display: none !important;
}
.cell-likes div {
  margin-left: -5px;
  margin-right: -5px;
}

@media (max-width: 768px) {
  .game-logs-modal-container .summary {
    flex-direction: column;
  }
  .game-logs-modal-container {
    background: #f9f9f9;
  }
  .cell-likes div {
    margin-left: unset;
  }
  div.table .dont-have-game-msg div {
    font-size: 0.75em;
    font-weight: 600;
  }
  .game_header {
    height: 62px;
  }
  .game_header .main_header {
    padding: 8px 16px;
  }
  div.table .table-row,
.table-header {
    margin-right: unset;
  }

  div.table div.table-row div.table-cell button {
    margin-bottom: -5px;
  }
  .game_header .main_header a.game_logo {
    width: 84px;
    height: 34px;
    margin-right: 20px;
    background-image: url('../../public/img/game-logo-mobile.svg');
  }

  .game_header .main_header .header_action_panel button span.username
    /* ,.game_header .main_header .header_action_panel button svg  */
 {
    display: none;
  }
  .contents_wrapper {
    height: initial;
  }
}
